import uniCollapse from '@/components/uni-ui/uni-collapse/uni-collapse.vue'
import uniCollapseItem from '@/components/uni-ui/uni-collapse-item/uni-collapse-item.vue'
import kList from '@/components/k-ui/k-team/k-user.vue'
import kPoster from '@/components/k-ui/k-user-list/k-list.vue'
import uniNumberBox from "@/components/uni-ui/uni-number-box/uni-number-box.vue"
import uniLink from "@/components/uni-ui/uni-link/uni-link.vue"
import minActionSheet from '@/components/min-action-sheet/min-action-sheet.vue'
import {
	mapState
} from 'vuex';
export default {
	props: {},
	components: {
		uniCollapse,
		uniCollapseItem,
		kList,
		kPoster,
		uniNumberBox,
		uniLink,
		minActionSheet
	},
	computed: {
		...mapState(['hasLogin', 'userInfo']),
		countApplicant() {
			if (this.datas == "") return 0
			return this.datas.team.applicant.length
		},
		countMembers() {
			if (this.datas == "") return 0
			return this.datas.team.members.length
		},
		needNum() {
			if(this.datas == "") return 0
			if(this.datas.team.size - this.datas.team.members.length < 0) return 0
			return this.datas.team.size - this.datas.team.members.length
		},
		showContact() {
			if(this.user=="" || this.datas == "") return false
			// console.log(this.user.id)
			// console.log(this.datas.leader.id)
			// console.log(this.datas.team.isMember)
			// console.log(this.datas.team.isApplicant)
			if(this.user.id==this.datas.leader.id || this.datas.team.isMember || this.datas.team.isApplicant) return true
			if(this.datas.leader.id==1) return true
			return false
		}
	},
	mounted() {
		this.user = this.$store.getters.getUserInfo
	},
	data() {
		return {
			modalShow: '',
			teamName: '',
			contact: '',
			teamBrief: '',
			need: '',
			curTeamId: '',
			datas: [],
			game: [], // 另外存储，否则时间过滤会有bug
			leader: [],
			placeholder: "",
			user: '',
			// 模态框
			applyModalShow: false,
			applyModalInput: '',
			applyClearIconShow: false,
			// 
			actions1: [
			  {
			    name: '同意入队',
			    icon: 'cuIcon-roundcheckfill',
			    color: '#19be6b'
			  },
			  {
			    name: '拒绝入队',
			    icon: 'cuIcon-roundclosefill',
			    color: '#fa3534'
			  },
			  {
			    name: '查看信息',
			    icon: 'cuIcon-search',
			    color: '#007aff'
			  }
			],
			actions2: [
			  {
			    name: '移出队伍',
			    icon: 'cuIcon-roundclosefill',
			    color: '#fa3534'
			  },
			  {
			    name: '查看信息',
			    icon: 'cuIcon-search',
			    color: '#007aff'
			  }
			],
		}
	},
	onLoad(options) {
		uni.showLoading({
			title: '正在加载'
		})
		this.curTeamId = options.id
		// this.curTeamId=1
		this.loadData()
	},
	onPullDownRefresh() {
		uni.showLoading({
			title: '正在加载'
		})
		this.loadData();
	},
	methods: {
		applicantOp (userId) {
			console.log(userId)
			let actions = this.actions1
			if(this.leader[0].id != this.user.id) {
				actions = []
				actions.push(this.actions1[2])
			}
			  this.$refs.as.handleShow({
				actions: actions,
				success: (res) => {
					if(actions.length==1) {
						this.userDetail(userId)
						return
					}
					switch (res.id) {
					// -1代表取消按钮
						case -1:
						  console.log(res)
						  break
						case 0:
						  this.agree(userId)
						  break
						case 1:
							this.disagree(userId)
							break
						case 2:
						 this.userDetail(userId)
						  break
					}
				}
			  })
			},			
		memberOp(userId) {
			let actions = this.actions2
			if(this.leader[0].id != this.user.id) {
				actions = []
				actions.push(this.actions2[1])
			}
		  this.$refs.as.handleShow({
		    actions: actions,
		    success: (res) => {
				if(actions.length==1) {
					this.userDetail(userId)
					return
				}
				switch (res.id) {
					// -1代表取消按钮
					case -1:
					  console.log(res)
					  break
					case 0:
					  this.kickOut(userId)
					  break
					case 1:
					  this.userDetail(userId)
					  break
				}
		    }
		  })
		},
		async isBoundWx(id){
			const res = await this.$api.request({
				url: '/isBoundWx/' + id,
			})
			if(res.data.isBoundWeChat == 0) {
				uni.showModal({
					title: '提示',
					content: '检测到该用户未关注‘i瓜大’公众号，将无法及时接收您的组队请求，建议自行联系~',
					showCancel: false,
					confirmText: '知道了',
					confirmColor:'#8c8b83',
					success(res2) {
					}
				});
			}
		},
		applyShowModal() {
			if(this.leader[0].id == 1) {
				uni.showModal({
					title: '提示',
					content: '官方转发信息，请自行联系~',
					showCancel: false,
					confirmText: '知道了',
					confirmColor:'#8c8b83',
					success(res2) {
					}
				});
				return
			}
			if (!this.check()) {
				return
			}
			this.applyModalShow = true
		},
		applyModalConfim() {
			if(this.applyModalInput=='') {
				this.$api.msg("请完善信息~")
				return
			}
			this.apply(this.datas.team.id)
			this.applyHideModal()
		},
		applyClearIcon(){
			this.applyModalInput = ''
		},
		applyModalInputing(){
			if(this.applyModalInput.length > 0) {
				this.applyClearIconShow = true
			}else {
				this.applyClearIconShow = false
			}
		},
		applyHideModal(){
			this.applyModalShow = false
			this.applyModalInput = ''
		},
		needChange(e) {
			this.need = e
			if (e == this.game.teamSize) {
				this.$api.msg("不大于队伍最大人数~")
			}
			if(this.datas != "") {
				if(e==this.datas.team.members.length) {
					this.$api.msg("不小于队伍中的人数~")
				}
			}

		},
		complete(msg) {
			uni.showModal({
				title: '提示',
				content: msg,
				showCancel: true,
				confirmText: '去设置',
				success: (e) => {
					if (e.confirm) {
						uni.navigateTo({
							url: '/pages/set/personInfo/personInfo'
						})
					}
				}
			})
		},
		check() {
			if (this.user.contact == "") {
				this.complete("请完善您的联系方式~")
				return false
			}
			return true
		},
		unApply(id) {
			uni.showModal({
				title: '取消提示',
				content: '两分钟内无法再次申请，您确定取消申请吗？',
				showCancel: true,
				confirmText: '确定',
				success: async (e) => {
					if (e.confirm) {
						uni.showLoading({
							title: '正在取消...'
						})
						const res = await this.$api.request({
							url: '/team/unApply/' + id,
						})
						uni.hideLoading()
						if (res.status == 200) {
							this.loadData()
							uni.showToast({
								title:'取消成功~'
							})
							setTimeout(()=>{
								if (this.$api.prePage().loadData) {
									this.$api.prePage().loadData()
								}
							}, 1000)
						} else if (res.status.startsWith("1000")) {
							this.$api.msg(res.msg)
						} else {
							this.$api.msg("系统正忙~")
						}
					}
				}
			})
		},
		async apply(id) {
			const res = await this.$api.request({
				url: '/team/apply/' + id,
				method: 'POST',
				data:{
					remark: this.applyModalInput
				}
			})
			// console.log(res)
			if (res.status == 200) {
				this.loadData()
				uni.showToast({
					title:'申请成功~'
				})
				setTimeout(()=>{
					if (this.$api.prePage().loadData) {
						this.$api.prePage().loadData()
					}
					console.log("----------------")
					console.log(this.datas)
					console.log(this.datas.leader.id)
					this.isBoundWx(this.datas.leader.id)
				}, 1000)
			} else if (res.status.startsWith("1000")) {
				this.$api.msg(res.msg)
			} else {
				this.$api.msg("系统正忙~")
			}
		},
		quit(id) {
			uni.showModal({
				title: '退出提示',
				content: '2分钟内无法再次申请，您确定退出队伍吗？',
				showCancel: true,
				confirmText: '确定',
				success: async (e) => {
					if (e.confirm) {
						uni.showLoading({
							title: '正在退出...'
						})
						const res = await this.$api.request({
							url: '/team/unApply/' + id,
						})
						uni.hideLoading()
						if (res.status == 200) {
							this.loadData()
							uni.showToast({
								title:'退出成功~'
							})
							setTimeout(()=>{
								if (this.$api.prePage().loadData) {
									this.$api.prePage().loadData()
								}
							}, 1000)
						} else if (res.status.startsWith("1000")) {
							this.$api.msg(res.msg)
						} else {
							this.$api.msg("系统正忙~")
						}
					}
				}
			})
		},
		showModal(e) {
			// console.log(this.datas)
			// console.log(this.datas.team.size)
			this.teamName = this.datas.team.name
			this.contact = this.datas.team.contact
			this.teamBrief = this.datas.team.brief
			this.need = this.datas.team.size
			this.modalShow = true
		},
		hideModal(e) {
			this.modalShow = false
		},
		confirmModal() {
			// console.log(this.teamName)
			// console.log(this.teamBrief)
			if (this.teamName == '' || this.teamBrief == '' || this.contact == '') {
				this.$api.msg("请完善信息~")
				return;
			}
			this.update()
			this.hideModal()
		},
		async update(id) {
			// uni.showLoading()
			const res = await this.$api.request({
				url: '/team/update',
				data: {
					teamId: this.curTeamId,
					name: this.teamName,
					contact: this.contact,
					size: this.need,
					brief: this.teamBrief
				}
			})
			this.showTip(res.status, res.msg)
		},
		userDetail(id) {
			uni.navigateTo({
				url: '/pages/person/person-detail?id=' + id
			})
		},
		finish(){
			const _this = this
			uni.showModal({
				title: '完成组队',
				content: '您已组队成功了吗？',
				showCancel: true,
				confirmText: '是的',
				success: (e) => {
					if (e.confirm) {
						_this.finishTeam()
					}
				}
			})
		},
		async finishTeam(id) {
			uni.showLoading()
			const res = await this.$api.request({
				url: '/team/update',
				data: {
					teamId: this.curTeamId,
					size: 0
				}
			})
			console.log(res)
			uni.hideLoading()
			if (res.status == 200) {
				uni.navigateBack()
				if (this.$api.prePage().loadData) {
					this.$api.prePage().loadData()
				}
				this.$api.msg("恭喜您组队成功~")
			} else if (res.status.startsWith("1000")) {
				this.$api.msg(res.msg)
			} else {
				this.$api.msg("服务器正忙~")
			}
		},
		dissolve() {
			const _this = this
			uni.showModal({
				title: '解散队伍',
				content: '您确定解散队伍吗？',
				showCancel: true,
				confirmText: '解散',
				success: (e) => {
					if (e.confirm) {
						_this.dissolveTeam()
					}
				}
			})
		},
		async dissolveTeam(id) {
			uni.showLoading()
			const res = await this.$api.request({
				url: '/team/dissolve/' + this.curTeamId
			})
			console.log(res)
			uni.hideLoading()
			if (res.status == 200) {
				uni.navigateBack()
				if (this.$api.prePage().loadData) {
					this.$api.prePage().loadData()
				}
				uni.showToast({
					title:'队伍已解散~'
				})
			} else if (res.status.startsWith("1000")) {
				this.$api.msg(res.msg)
			} else {
				this.$api.msg("服务器正忙~")
			}
		},
		async loadData() {
			if(this.curTeamId==undefined || this.curTeamId=="") {
				uni.navigateBack()
				return
			}
			const res = await this.$api.request({
				url: '/team/teamDetail/' + this.curTeamId
			})
			console.log(res)
			this.datas = res.data
			this.game = res.data.game
			this.leader[0] = res.data.leader
			// console.log(res.data.team.members)
			// console.log(res.data.team.applicant)
			uni.stopPullDownRefresh();
			uni.hideLoading()
		},
		showTip(status, msg) {
			console.log(status)
			console.log(msg)
			// setTimeout(() => {
				// uni.hideLoading()
				if (status.startsWith("1000")) {
					this.$api.msg(msg)
				} else if (status == "200") {
					this.loadData()
					uni.showToast({
						title:'操作成功~'
					})
				} else {
					this.$api.msg("系统正忙~")
				}
				
			// }, 1000)
		},
		async kickOut(id) {
			console.log(this.curTeamId)
			console.log(id)
			uni.showLoading()
			const res = await this.$api.request({
				url: '/team/deleteApply',
				data: {
					teamId: this.curTeamId,
					applyId: id
				}
			})
			this.showTip(res.status, res.msg)
		},
		async agree(id) {
			uni.showLoading()
			const res = await this.$api.request({
				url: '/team/agree',
				data: {
					teamId: this.curTeamId,
					applyId: id
				}
			})
			this.showTip(res.status, res.msg)
		},
		async disagree(id) {
			uni.showLoading()
			const res = await this.$api.request({
				url: '/team/disagree',
				data: {
					teamId: this.curTeamId,
					applyId: id
				}
			})
			this.showTip(res.status, res.msg)
		},
		// 跳转到比赛详情
		goGameDetail(id) {
			uni.navigateTo({
				url: '/pages/game/game-detail?id=' + id
			})
		},
	},
	onShareAppMessage() {
		// uni.share({
		//     provider: "qq",
		//     scene: "WXSceneSession",
		//     type: 1,
		//     summary: "我正在使用HBuilderX开发uni-app，赶紧跟我一起来体验！",
		//     success: function (res) {
		//         console.log("success:" + JSON.stringify(res));
		//     },
		//     fail: function (err) {
		//         console.log("fail:" + JSON.stringify(err));
		//     }
		// });
		return {
			title: '快来加入我的队伍吧~',
			// imageUrl: 'https://easycampus-asset.oss-cn-shenzhen.aliyuncs.com/sharebg.png',
			path: '/pages/user/team/my-team-detail?id=' + this.curTeamId
		}
	},
}
